@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Public+Sans:wght@100;400;800&display=swap');

:root {
    --color-action: #fa2a33;
    --color-bg-action: rgba(255, 0, 0, 0.1);
    --color-bg-dark: #222;
    --color-bg-dark-highlight: #343434;
    --font-text: "Public Sans", sans-serif;
    --font-sans: "Bebas Neue", cursive;
    --color-links: white;
}

html, body {
    background: var(--color-bg-dark);
    margin: 0;
    color: #eee;
    font-family: var(--font-text);
    font-size: 18px;
    text-rendering: optimizeLegibility;
}

h1, h2 {
    text-align: center;
    font-family: var(--font-sans);
    text-transform: uppercase;
}

h2 {
    font-size: 1.4rem;
}

p {
    line-height: 1.4;
    margin-bottom: 1.6rem;
    margin-top: 0;
}

a {
    color: var(--color-links)
}

sup {
    font-size: .8rem;
}

strong {
    color: var(--color-action);
}

.page-content {
    position: relative;
    perspective: 20px;
    height: 100vh;
    overflow-x: hidden;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    transform-style: preserve-3d;

    &::before {
        content: ' ';
        position: absolute;
        top: 0;
        width: 100vw;
        height: 400vh;
        background: url('./assets/wc-logo.svg') no-repeat center top;
        background-size: cover;
        transform: translateZ(6px) scale(.75);
        z-index: -1;
    }

    &::after {
        content: ' ';
        position: absolute;
        left: -50vw;
        top: 15vh;
        height: 30vh;
        width: 300%;
        background: var(--color-bg-action);
        transform: rotate(-30deg) translateZ(8px) scale(.75);
        z-index: 0;
    }
}

.stripes {
    transform: translateZ(8px) scale(.75);

    &::before {
        content: ' ';
        position: absolute;
        left: -80vw;
        top: 15vh;
        height: 30vh;
        width: 300%;
        background: var(--color-bg-action);
        transform: rotate(-30deg);
        z-index: 0;
    }

    &::after {
        content: ' ';
        position: absolute;
        left: -80vw;
        top: 35vh;
        height: 30vh;
        width: 300%;
        background: var(--color-bg-action);
        transform: rotate(-30deg) translateZ(2px) scale(.75);
        z-index: 0;
    }
}

.header-title {
    font-size: 4rem;
    position: relative;
    z-index: 1;
}

.intro {
    position: relative;
    padding: 0 2rem;
    text-align: center;
    z-index: 1;
}

.block-action {

}

.manifesto {
    padding: 2rem;
    margin-bottom: 2rem;
    z-index: 1;

    h2 {
        margin-bottom: 4rem;
        font-size: 2rem;
    }

    ul {
        list-style: none;
        padding: 0;
    }

    li {
        margin-bottom: 4rem;
    }

    .horror {
        font-family: var(--font-sans);
        text-transform: uppercase;
        font-size: 1.4rem;
        margin-bottom: .5rem;
        display: block;
    }

    .horror-detail {
        line-height: 1.4;
    }

    sup a {
        color: #888;
    }
}

.conclusion {
    font-size: 2rem;
    position: relative;
    margin: 0 -2rem 6rem -2rem;
    padding: 0 2rem 16rem 2rem;
    background: url('./assets/worldcup.svg') bottom left repeat-x;
    background-position-x: -42px;
    background-size: 100px;

    span {
        position: relative;
        z-index: 1;
    }

    &::after {
        content: ' ';
        position: absolute;
        left: -50vw;
        top: 20vh;
        height: 30vh;
        width: 300%;
        background: var(--color-bg-action);
        transform: rotate(-30deg) translateZ(8px) scale(.75);
        transform-origin: 0 50%;
        z-index: 0;
    }
}

.manifesto-bridge {
    font-family: var(--font-sans);
    font-size: 2rem;
    text-align: center;
    padding: 0 2rem;
}

.block-action {
    position: relative;
    margin-bottom: 6rem;

    .action-order {
        display: block;
        text-align: center;
        font-size: 8rem;
        font-family: var(--font-sans);
        color: #555;
        z-index: 0;
    }

    h2 {
        position: relative;
        z-index: 1;
    }

    .action-explain {
        position: relative;
        z-index: 1;
        padding: 2rem;
    }
}

.action-explain {
    position: relative;
    z-index: 1;
}

.engage-container {
    position: relative;
    z-index: 0;
    padding: 0 2rem;
}
    .counter {
        font-size: 1.6rem;
        font-family: var(--font-sans);
    }

    .counter-value {
        display: block;
        font-size: 6rem;
    }

    .vote-container {
        position: relative;
        z-index: 1;
    }

    .recaptcha-container {
        margin-top: 2rem;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .vote-form {
        padding: 1rem 0;

        button {
            font-size: 2rem;
            padding: .5rem 1rem;
            border-radius: 10px;
            color: white;
            border: 3px solid var(--color-action);
            background: var(--color-bg-action);
            cursor: pointer;
            opacity: 1;
            transition: opacity 500ms ease-in;

            &:disabled {
                opacity: .2;
                pointer-events: none;
            }
        }
    }

    .success-msg {
        position: relative;
        z-index: 1;
        padding: 1rem;
        border: 3px solid var(--color-action);
        background: var(--color-bg-action);
    }

.avatar-tool {
    position: relative;
    z-index: 1;
    text-align: center;
    margin-top: 5rem;
}

.uploader {
    height: 33vh;
    background: var(--color-bg-highlight);
    display: flex;
    padding: 2rem;
    position: relative;
    z-index: 1;

    .upload-button {
        flex: 1 1 auto;
        padding: .5rem 1rem;
        border: 5px dashed #444;
        color: white;
        background: #333;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 10px;
        cursor: pointer;
    }
}

.avatar-renders {
    display: flex;
    justify-content: center;
    padding-top: 1rem;
}

.avatar-renders canvas {
    width: 120px;
    height: 120px;
    pointer-events: none;
}

img {
    display: block;
}

.controls {
    list-style: none;
    padding: 1rem;
    margin: 0;
    text-align: center;

    li {
        margin-bottom: 1rem;
    }

    button {
        border: 1px solid #888;
        border-radius: 5px;
        padding: .25rem .75rem;
        background: #444;
        color: #ddd;
        cursor: pointer;
    }

    .button-primary {

    }

    .button-secondary {
        border: 0;
        background: transparent;
        padding: 0;
        font-size: .8rem;
        color: #aaa;
    }
}

.footer {
    padding: 2rem 1rem;
    background: var(--color-bg-dark-highlight);

    ol li {
        margin-bottom: 1.3rem;
    }
}

@media only screen and (min-width: 812px) {
    .page-content {

    }

    header {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100vh;
        text-align: center;

        .header-title {
            font-size: 6rem;
            font-weight: 700;
            margin: 0;
        }

        .intro {
            font-size: 1.4rem;
            padding: 0 4rem;
        }
    }

    .manifesto {
        padding: 0;

        h2 {
            height: 15vh;
            text-align: center;
            position: sticky;
            top: 0;
            margin: 0 0 4rem 0;
            padding: 1rem 2rem 2rem 1rem;
            background: linear-gradient(var(--color-bg-dark), var(--color-bg-dark), rgba(0,0,0,0));
        }

        ul {
            list-style: none;
            padding: 0 2rem 0;
        }

        li {
            margin-bottom: 30vh;
            display: flex;
            font-size: 1.6rem;
            line-height: 1.6;

            .horror {
                flex: 0 0 33.33%;
                font-size: 3rem;
                line-height: 1;
                font-family: var(--font-sans);
                padding: 3rem;
                font-weight: 700;
            }

            .horror-detail {
                padding: 3rem;
                margin-top: -.5rem;
            }
        }
    }

    .conclusion {
        text-align: center;
        padding: 0 4rem 16rem 4rem;
        position: relative;

        &::after {
            top: 35vh;
            transform: rotate(-15deg);
        }
    }

    .actions-list {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
    }

    .block-action {
        grid-column: span 3;
        text-align: center;
        font-size: 1.4rem;

        .action-order {
            position: absolute;
            left: 2rem;
            top: -2rem;
            font-size: 15rem;
        }

        h2 {
            font-size: 3rem;
            margin-bottom: 0;
        }
    }

    .action-second {
        grid-column: 2 / 5;
    }

    .avatar-tool {
        display: grid;
        grid-template-columns: 1fr 1fr;
    }
}
